// 
interface Complex {
  r: number;
  i: number;
}
interface WaveData {
  time: number;
  value: number;
}
interface ComplexPlaneChartProps {
  data: Complex[];
  centroid: Complex;
  title: string;
}
interface LabeledVerticalSliderProps extends SliderProps {
  label: string;
}
interface LabeledHorizonSliderProps extends SliderProps {
  label: string;
}
interface CustomizedLabelProps {
  x: number;
  y: number;
  stroke: string;
  value: string;
}
class CustomizedLabel extends PureComponent<CustomizedLabelProps> {
  render() {
    const { x, y, stroke, value } = this.props;

    return (
      <text
        x={x}
        y={y}
        dy={-10}
        dx={10}
        fill={stroke}
        fontSize={16}
        textAnchor="middle"
      >
        {value}
      </text>
    );
  }
}

// 
const WaveChart = ({
  data,
  color,
  title,
}: {
  data: WaveData[];
  color: string;
  title: string;
}) => {
  return (
    <Box>
      <Typography variant="h6" align="center">
        {title}
      </Typography>
      <ResponsiveContainer width="100%" height={300}>
        <LineChart data={data}>
          <CartesianGrid strokeDasharray="3 3" />
          <XAxis dataKey="time" />
          <YAxis domain={[-5, 5]} />
          <Line type="monotone" dot={false} dataKey="value" stroke={color} />
        </LineChart>
      </ResponsiveContainer>
    </Box>
  );
};
const CompositeChart = ({
  data,
  color,
  title,
}: {
  data: WaveData[];
  color: string;
  title: string;
}) => {
  return (
    <Box>
      <Typography variant="h6" align="center">
        {title}
      </Typography>
      <ResponsiveContainer width="100%" height={300}>
        <LineChart data={data}>
          <CartesianGrid strokeDasharray="3 3" />
          <XAxis dataKey="time" />
          <YAxis domain={[-15, 15]} />
          <Line type="monotone" dot={false} dataKey="value" stroke={color} />
        </LineChart>
      </ResponsiveContainer>
    </Box>
  );
};
const SpectrumChart = ({
  title,
  data,
  frequencyRange,
  color,
  dataKey,
  selectedFrequency,
  dominMinY,
  dominMaxY,
}: {
  title: string;
  data: Array<{ frequency: number; [key: string]: number | string }>;
  frequencyRange: number[];
  color: string;
  dataKey: string;
  selectedFrequency: number;
  dominMinY: number;
  dominMaxY: number;
}) => {
  // frequencyRange 
  const filteredData = data.filter(
    (item) =>
      item.frequency >= frequencyRange[0] && item.frequency <= frequencyRange[1]
  );

  return (
    <Box>
      <Typography variant="h6" align="center">
        {title}
      </Typography>
      <ResponsiveContainer width="100%" height={300}>
        <LineChart data={filteredData}>
          <CartesianGrid strokeDasharray="3 3" />
          <XAxis
            dataKey="frequency"
            domain={[frequencyRange[0], frequencyRange[1]]}
            type="number"
            scale="linear"
            tickFormatter={formatFrequency}
          />
          <YAxis domain={[dominMinY, dominMaxY]} />
          <Tooltip />
          <ReferenceLine x={selectedFrequency} stroke="red" label="DFT freq" />
          <Line type="monotone" dot={false} dataKey={dataKey} stroke={color} />
        </LineChart>
      </ResponsiveContainer>
    </Box>
  );
};
const ComplexPlaneChart: React.FC<ComplexPlaneChartProps> = ({
  title,
  data,
  centroid,
}) => {
  const chartData = data.map((point, index) => ({
    x: point.r,
    y: point.i,
    index,
  }));
  const [width, height] = useWindowSize();
  const chartSize = Math.min(600, width - 30);

  return (
    <Box width={chartSize} height={chartSize}>
      <Typography variant="h6" align="center">
        {title}
      </Typography>
      <ResponsiveContainer>
        <LineChart>
          {calcRefLines(-15, 15, 5).map((line) => (
            <ReferenceLine key={line.key} x={line.value} stroke="lightgrey" />
          ))}
          {calcRefLines(-15, 15, 5).map((line) => (
            <ReferenceLine key={line.key} y={line.value} stroke="lightgrey" />
          ))}
          <XAxis type="number" dataKey="y" name="Real" domain={[-15, 15]} />
          <YAxis
            type="number"
            dataKey="x"
            name="Imaginary"
            domain={[-15, 15]}
          />
          <Line
            data={chartData}
            type="monotone"
            dot={false}
            dataKey="x"
            stroke="#8884d8"
          />
          <Line
            data={[
              { x: centroid.r.toFixed(2), y: centroid.i.toFixed(2) },
              { x: 0, y: 0 },
            ]}
            dataKey="x"
            stroke="red"
            strokeWidth={4}
            fill="red"
            label={({ x, y, stroke, index }) =>
              index === 0 ? (
                <CustomizedLabel
                  x={x}
                  y={y}
                  stroke={stroke}
                  value={`X:${centroid.r.toFixed(2)}, Y:${centroid.i.toFixed(
                    2
                  )}`}
                />
              ) : (
                <svg></svg>
              )
            }
          />
        </LineChart>
      </ResponsiveContainer>
    </Box>
  );
};
const calcRefLines = (min: number, max: number, step: number) => {
  const lines = [];
  for (let i = min; i <= max; i += step) {
    lines.push({ key: `line-${i}`, value: i });
  }
  return lines;
};
function useWindowSize() {
  const [size, setSize] = useState([window.innerWidth, window.innerHeight]);
  useEffect(() => {
    const handleResize = () => {
      setSize([window.innerWidth, window.innerHeight]);
    };
    window.addEventListener("resize", handleResize);
    return () => window.removeEventListener("resize", handleResize);
  }, []);
  return size;
}
